<template>
  <div class="buy-airtime">
  	<div style="position: fixed; top: 50%; left: 50%; z-index: 999;">
      <atom-spinner
        v-if="loading"
        :animation-duration="speed"
        :size="config.size"
        :color="spinnersColor"
      />
    </div>
    <div class="mb-3" v-if="processingError">
      <va-notification color="danger" closeable>       
        An error occurred! Please Try again later.
      </va-notification>
    </div>
    <div class="row">
      <div class="flex xs12 md6">
      	 <va-card :title="$t('menus.buy-airtime')">
          <form @submit.prevent="onsubmit">
            

            <va-input
              :label="$t('amount')"
              type="number"
              v-model="formdata.amount"
              :error="!!amountErrors.length"
              :error-messages="amountErrors"
            />         
            <va-input              
              :label="$t('recieving_phone_no')"
              v-model="formdata.recieving_phone_no"           
              :error="!!phoneErrors.length"
              :error-messages="phoneErrors"
            />	

			<div class="row mt-5">            
				<va-button type="submit" >
				{{ $t('forms.controls.submit') }}
				</va-button>                        
			</div>
          </form>
      	 </va-card>
      </div>
    </div>
    <va-modal
      v-model="showConfirmModal"
      position="top"
      size = "medium"
      :title=" $t('confirm_trans')"
      cancelClass="none"      
      :okText=" $t('modal.confirm') "     
      :cancelText=" $t('modal.cancel')"      
      noOutsideDismiss
      noEscDismiss
      @ok = "FinalPost"
    >
      <table class="va-table va-table--striped va-table--hoverable" width="100%">
        <tbody>
          <tr>
            <td>Recieving Phone No.</td>
            <td style="text-align: right;">{{ phone_no }}</td>
          </tr>
          <tr>
            <td>Airtime amount</td>
            <td style="text-align: right;">{{ payment_amount | Currency }}</td>
          </tr>         
          <tr>
            <td>Transaction Charge</td>
            <td style="text-align: right;">{{ charge | Currency }}</td>
          </tr>                  
        </tbody>
      </table>
    </va-modal>
  </div>
</template>
<script>
import axios from 'axios'
import * as spinners from 'epic-spinners'

export default {
	components: {
	...spinners      
	},
	name: 'buy-airtime',
	data () {
		return {
			userData: JSON.parse(localStorage.getItem('LoggedUser')),
			token: JSON.parse(localStorage.getItem('Token')),	
			loading: false,
			loadSelect:false,
			processingError: false,	
			charge:0,			
			finalPost : '',
			formdata:{				
				recieving_phone_no:'',
				amount:'',				       
				user: '',
			},
			amountErrors : [],			
			phoneErrors: [],
			payment_amount : 0,
			phone_no: '',			
			showConfirmModal : false,
			config: {
				size: 80,
				duration: 1500,
			},
			spinnersColor: this.$themes.secondary,
			currentDuration: 1500,
			sliderDuration: {
				min: 1000,
				max: 2000,
			}      
		}
	},
	computed: {
	    speed () {
	    	return this.sliderDuration.min + this.sliderDuration.max - this.currentDuration
	    },

	    formReady () {
	    	return !this.amountErrors.length && !this.phoneErrors.length
	    },
	},
	methods : {
	    onsubmit () {	       
	        this.processingError = false;
	        this.amountErrors = this.formdata.amount ? [] : ['Amount is required'];
	        this.phoneErrors = this.formdata.recieving_phone_no ? [] : ['Phone No. is required'];
	        if (this.formReady) {
		        this.loading = true	       
		        var self = this

		        let config = {
		          headers: {
		            Authorization: this.userData.user_id+' '+this.token
		          }
		        }

		        axios.post(`${this.$apiUrl}BuyAirtime`, this.formdata, config)
		        .then(function (res) {            
		        	self.loading = false
		        	const response = res.data          			
		        	if (response.status == false) {
		        		self.amountErrors =  response.message_code;
		        		self.phoneErrors = response.message_code;
		        	}

		        	if(response.status == true){           
			            self.payment_amount = self.formdata.amount
			            self.phone_no = self.formdata.recieving_phone_no
			            self.showConfirmModal = true;
			        } 
		        })
		        .catch(function (error) {
		            console.log(error)
		        })
		    }
		},

		FinalPost () {		      
		    this.loading = true
		    var self = this

		    let config = {
          headers: {
            Authorization: this.userData.user_id+' '+this.token
          }
        }

		    axios.post(`${this.$apiUrl}BuyAirtimefinal`, this.formdata, config)
		      .then(function (res) {
		        self.loading = false
		        const resp = res.data
		        if(resp.success == true){              
		          self.showToast(`Payment Processed succefully`, {
		            position: 'bottom-right',
		            icon: "fa-check",
		            duration: 2500,
		          })
		          setTimeout( () => self.$router.go(-1), 2500);
		        } else {
		          self.processingError = true
		        }
		                  
		      })
		      .catch(function (error) {
		          console.log(error)
		      })
		},
	}, 
	created () {
	    this.formdata.user  = this.userData.user_id
	}
}
</script>